@import 'sanitize.css/sanitize.css';
@import 'sanitize.css/forms.css';
@import 'sanitize.css/assets.css';
@import 'sanitize.css/typography.css';
@import 'sanitize.css/reduce-motion.css';
:root {
  --color-black: #000;
  --color-white: #fff;
  --pink: rgba(255, 154, 180, 0.20);
  --red: #F10132;
  --box-shadow-red: 0 2.6px 3.1px 0 rgba(241, 1, 50, 0.02), 0 11.6px 6.5px 0 rgba(241, 1, 50, 0.04), 0 28.4px 13px 0 rgba(241, 1, 50, 0.05), 0 54.7px 25.5px 0 rgba(241, 1, 50, 0.06), 0 92px 46.9px 0 rgba(241, 1, 50, 0.08), 0 142px 80px 0 rgba(241, 1, 50, 0.10);
  --border-card: rgba(241, 1, 50, 0.50);
  --box-shadow-card: 0px 1.9px 3.1px 0px rgba(0, 0, 0, 0.05), 0px 8.1px 6.5px 0px rgba(0, 0, 0, 0.04), 0px 20px 13px 0px rgba(241, 1, 50, 0.03), 0px 38.5px 25.5px 0px rgba(241, 1, 50, 0.03), 0px 64.8px 46.9px 0px rgba(241, 1, 50, 0.02), 0px 100px 80px 0px rgba(0, 0, 0, 0.01);
  --box-shadow-card-disabled: 0 1.9px 3.1px 0 rgb(0 0 0 / 5%), 0 8.1px 6.5px 0 rgb(0 0 0 / 4%), 0 20px 13px 0 rgb(241 1 50 / 3%), 0 38.5px 25.5px 0 rgb(241 1 50 / 3%), 0 64.8px 46.9px 0 rgb(241 1 50 / 2%), 0 100px 80px 0 rgb(0 0 0 / 1%);
  --box-shadow-active-input: 0px 1.9px 3.1px 0px rgba(0, 0, 0, 0.05), 0px 8.1px 6.5px 0px rgba(0, 0, 0, 0.04), 0px 20px 13px 0px rgba(241, 1, 50, 0.03), 0px 38.5px 25.5px 0px rgba(241, 1, 50, 0.03), 0px 64.8px 46.9px 0px rgba(241, 1, 50, 0.02), 0px 100px 80px 0px rgba(0, 0, 0, 0.01);
  --font-base: Raleway, sans-serif, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
  "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
  "Segoe UI Symbol";
  --font-dance: Dance, sans-serif;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 100%;
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 300;
  font-family: Raleway;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Raleway/Raleway-Light.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: Raleway;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Raleway/Raleway-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 500;
  font-family: Raleway;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Raleway/Raleway-Medium.woff2") format("woff2");
}
@font-face {
  font-weight: 700;
  font-family: Raleway;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Raleway/Raleway-Bold.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: Dance;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Dance/dance_partner.woff2") format("woff2");
}
.burger__button {
  position: relative;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.burger__button span {
  position: absolute;
  top: 50%;
  left: calc(50% - 11px);
  width: 22px;
  height: 2px;
  background-color: var(--color-black);
  border-radius: 12px;
  transform: translate(0, -50%);
  transition: all 0.5s;
}
.burger__button span:nth-of-type(2) {
  top: calc(50% - 8px);
  width: 17px;
}
.burger__button span:nth-of-type(3) {
  top: calc(50% + 7px);
  width: 12px;
}
.burger__menu {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 250px;
  height: 100vh;
  padding: 15px 40px;
  background: var(--color-white);
  z-index: 300;
  transform: translate(250px, 0);
  flex-direction: column;
}
.burger__menu--active {
  display: flex;
  transform: translate(0, 0);
}
.burger__logo {
  margin-bottom: 20px;
}
.burger__inf-company a {
  font-size: 20px;
  letter-spacing: 0.4px;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
}
.burger__phone {
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-size: 20px;
  letter-spacing: 0.4px;
}
.burger__btn {
  width: 100%;
  margin-top: 25px;
}
.burger__nav {
  margin-top: 110px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px 0;
}
@media screen and (min-width: 768px) {
  .burger__nav {
    display: none;
  }
}
.burger__nav a {
  width: 100%;
  text-align: center;
  font-size: 15px;
  letter-spacing: 0.3px;
}
.burger__social-media {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  width: 100%;
  justify-content: center;
}
.burger__block {
  display: flex;
  align-items: center;
  gap: 0 15px;
}

.button {
  color: var(--color-black);
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.4px;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
  transition: 0.3s ease;
}
.button--white {
  background: var(--color-white);
  border-radius: 5px;
  box-shadow: var(--box-shadow-red);
}
@media screen and (min-width: 992px) {
  .button--white {
    font-size: 16px;
    padding: 10px 20px;
  }
}
@media screen and (min-width: 1280px) {
  .button--white {
    font-size: 20px;
    padding: 18px 40px;
  }
}
.button--red {
  padding: 25px 40px;
  color: var(--color-white);
  background: var(--red);
  border-radius: 5px;
}
@media screen and (min-width: 768px) {
  .button--red {
    font-size: 16px;
  }
}
@media screen and (min-width: 0) {
  .button--red {
    padding: 15px 30px;
    font-weight: 500;
    font-size: 15px;
    letter-spacing: 0.3px;
  }
}
.button--red-shadow {
  padding: 25px 40px;
  color: var(--color-white);
  font-size: 25px;
  letter-spacing: 0.5px;
  background: var(--red);
  border-radius: 5px;
  box-shadow: var(--box-shadow-red);
}
@media screen and (min-width: 768px) {
  .button--red-shadow {
    font-size: 16px;
  }
}
@media screen and (min-width: 0) {
  .button--red-shadow {
    padding: 10px 20px;
    font-weight: 500;
    font-size: 15px;
    letter-spacing: 0.3px;
  }
}

.container {
  max-width: 2000px;
  margin: 0 auto;
}
@media screen and (min-width: 0) {
  .container {
    width: calc(100% - 30px);
  }
}
@media screen and (min-width: 1280px) {
  .container {
    width: calc(100% - 80px);
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
@media screen and (min-width: 0) {
  .header__container {
    padding: 15px 0;
  }
}
@media screen and (min-width: 1280px) {
  .header__container {
    padding: 35px 0;
  }
}
.header__inner {
  display: flex;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .header__inner {
    gap: 0 50px;
  }
}
@media screen and (min-width: 1440px) {
  .header__inner {
    gap: 0 100px;
  }
}
@media screen and (min-width: 1680px) {
  .header__inner {
    gap: 0 149px;
  }
}
.header__img {
  line-height: 0;
  max-width: 161px;
  max-height: 50px;
}
.header__nav {
  display: flex;
  gap: 0 50px;
}
@media screen and (min-width: 0) {
  .header__nav {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .header__nav {
    display: flex;
    gap: 0 30px;
  }
}
@media screen and (min-width: 1280px) {
  .header__nav {
    gap: 0 50px;
  }
}
.header__nav a {
  letter-spacing: 0.4px;
}
@media screen and (min-width: 768px) {
  .header__nav a {
    font-size: 16px;
  }
}
@media screen and (min-width: 1280px) {
  .header__nav a {
    font-size: 18px;
  }
}
@media screen and (min-width: 1440px) {
  .header__nav a {
    font-size: 20px;
  }
}
.header__inf-company {
  display: flex;
  align-items: center;
}
@media screen and (min-width: 0) {
  .header__inf-company {
    display: none;
  }
}
@media screen and (min-width: 992px) {
  .header__inf-company {
    display: flex;
    gap: 0 15px;
  }
}
@media screen and (min-width: 1280px) {
  .header__inf-company {
    gap: 0 40px;
  }
}
@media screen and (min-width: 1440px) {
  .header__inf-company {
    gap: 0 69px;
  }
}
.header__inf-company a {
  font-size: 30px;
  letter-spacing: 0.6px;
  font-variant-numeric: lining-nums proportional-nums;
}
@media screen and (min-width: 992px) {
  .header__inf-company a {
    font-size: 16px;
  }
}
@media screen and (min-width: 1280px) {
  .header__inf-company a {
    font-size: 20px;
  }
}
@media screen and (min-width: 1680px) {
  .header__inf-company a {
    font-size: 30px;
  }
}
.header__burger {
  display: block;
}
@media screen and (min-width: 992px) {
  .header__burger {
    display: none;
  }
}

.input {
  width: 100%;
  margin-bottom: 15px;
  padding: 20px 30px;
  color: var(--black, #000);
  font-size: 20px;
  line-height: 150%; /* 30px */
  letter-spacing: 0.4px;
  background: var(--color-white);
  border: 1px solid var(--border-card);
  border-radius: 5px;
  outline: none;
  opacity: 1;
  font-variant-numeric: lining-nums proportional-nums;
}
@media screen and (min-width: 0) {
  .input {
    padding: 15px 25px;
    font-size: 15px;
  }
}
.input::placeholder {
  opacity: 0.5;
}
.input:focus, .input:active {
  border: 1px solid var(--red);
  box-shadow: var(--box-shadow-active-input);
}

.link {
  color: var(--color-black);
  text-decoration: none;
}
.link:hover {
  opacity: 0.7;
}

.modal-window {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 300;
  width: 100%;
  max-height: 90%;
  background: var(--color-white);
  border-radius: 5px;
  transform: translate(-50%, -50%);
  visibility: hidden;
  opacity: 0;
  transition: 0.3s all;
}
@media screen and (min-width: 0) {
  .modal-window {
    padding: 25px;
    max-width: 400px;
  }
}
@media screen and (min-width: 992px) {
  .modal-window {
    padding: 50px 55px 65px;
    max-width: 560px;
  }
}
.modal-window--active {
  visibility: visible;
  opacity: 1;
}
.modal-window__cross {
  position: absolute;
  cursor: pointer;
}
@media screen and (min-width: 0) {
  .modal-window__cross {
    top: 5px;
    right: 5px;
  }
}
@media screen and (min-width: 992px) {
  .modal-window__cross {
    top: 20px;
    right: 20px;
  }
}
.modal-window__title {
  font-weight: 600;
  line-height: 120%; /* 30px */
}
@media screen and (min-width: 0) {
  .modal-window__title {
    font-size: 20px;
  }
}
@media screen and (min-width: 992px) {
  .modal-window__title {
    font-size: 25px;
  }
}
.modal-window__subtitle {
  margin-top: 15px;
  font-weight: 300;
  line-height: 152%; /* 30.4px */
  letter-spacing: 0.4px;
}
@media screen and (min-width: 0) {
  .modal-window__subtitle {
    font-size: 15px;
  }
}
@media screen and (min-width: 992px) {
  .modal-window__subtitle {
    font-size: 20px;
  }
}
@media screen and (min-width: 0) {
  .modal-window__form {
    margin: 30px 0;
  }
}
@media screen and (min-width: 992px) {
  .modal-window__form {
    margin: 50px 0;
  }
}
.modal-window__button {
  width: 100%;
}
.modal-window__personal-data a,
.modal-window__personal-data p {
  font-size: 15px;
  line-height: 133%;
  letter-spacing: 0.3px;
  font-variant-numeric: lining-nums proportional-nums;
}
.modal-window__personal-data p {
  margin: 0;
  font-weight: 300;
}
@media screen and (min-width: 0) {
  .modal-window__personal-data p {
    display: inline;
  }
}
@media screen and (min-width: 992px) {
  .modal-window__personal-data p {
    display: block;
  }
}
.modal-window__personal-data a {
  color: var(--red);
  font-weight: 600;
  text-decoration-line: underline;
}
@media screen and (min-width: 0) {
  .modal-window__personal-data a {
    padding-left: 5px;
  }
}
@media screen and (min-width: 992px) {
  .modal-window__personal-data a {
    padding-left: 0;
  }
}

.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(68, 68, 68, 0.5);
  z-index: 200;
  transition: 0.3s all;
}
.overlay--active {
  opacity: 1;
  visibility: visible;
}

