.overlay {
  $block-name: &; // #{$block-name}__element

  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(68, 68, 68, 0.5);
  z-index: 200;
  transition: 0.3s all;

  &--active{
    opacity: 1;
    visibility: visible;
  }
}
